<template>
  <div class="card">
    <div class="card-header text-capitalize">
      {{this.endpointType}} report
      <i class="fa fa-download"></i>
    </div>
    <div class="card-body">
      <form @submit.prevent="fetchReport">
        <div class="row">
          <div class="col-8">
            <label for>From date</label>
            <input type="date" v-model="fromDate" class="form-control" />
          </div>
          <div class="col-4">
            <button class="mt-4 btn btn-success btn-block" :disabled="loading || !fromDate">
              <template v-if="loading">
                Loading
                <i class="fa fa-spinner fa-spin"></i>
              </template>
              <template v-else>
                Download
                <i class="fa fa-download"></i>
              </template>
            </button>
          </div>
        </div>
        <a ref="downloadLink" class="d-none" download :href="downloadLink">dd2</a>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "quick-reports",
  data() {
    return {
      fromDate: null,
      loading: false,
      downloadLink: `file.csv`,
    };
  },
  props: {
    endpointType: {
      default: "academies",
      type: String,
    }
  },
  methods: {
    async actionDownload(url) {
      url =  url.split("/").filter((e,index) => index > 7).join("/") 
      this.downloadLink = await `${process.env.VUE_APP_ROOT_API}/${url}`;
      this.loading = await false;
      await this.$refs.downloadLink.click();
      await this.$toasted.show("Report generated", {
        position: "top-right",
        duration: 5000,
        type: "success",
        closeOnSwipe: true,
      });
    },
    fetchReport() {
      this.loading = true;
      this.$http
        .get(
          `/api/v1/admin/reports/${this.endpointType}?start_date=${this.fromDate}`,
          {
            headers: {
              "content-type": "application/json",
              Authorization: this.$store.getters.authToken,
            },
          }
        )
        .then((response) => {
          console.log(response.data.url);
          this.actionDownload(response.data.url);
        })
        .catch((error) => {
          this.$toasted.show(error.response.data.error, {
            position: "top-right",
            duration: 5000,
            type: "error",
            closeOnSwipe: true,
          });
          this.loading = false;
        });
    },
  },
};
</script>