import { serverBus } from "@/main";

export default {
  name: 'kyc-validation',
  data() {
    return {
      loading: false,
      node: {
        username: null
      }
    };
  },
  watch: {
    "node.data.txidIncoming": function (newVal) {
      this.node.data.txid = `${this.$store.getters.currentUser.username}-${newVal}`;
    }
  },
  created() {
    serverBus.$on("kyc-modal-validation", (node) => {
      console.log(node)
      this.node = node;
      this.loading = false;
      this.showModal();
    });
  },
  beforeDestroy() {
    serverBus.$off("kyc-modal-validation");
  },
  methods: {
    showModal() {
      this.$root.$emit("bv::show::modal", "kyc-modal");
    },
    hideModal() {
      this.$root.$emit("bv::hide::modal", "kyc-modal");
    },
    secondVerification() {
      const answer = window.confirm('Are you sure you want to validate this profile?')
      if (answer) return true
    },
    validateProfile() {
      this.loading = true;
      if (this.secondVerification()) {
        this.$http({
          url: "/api/v1/admin/users/" + this.node.uuid,
          data: { kyc_validated: true },
          method: "PUT",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken
          }
        })
          .then(() => {
            this.$toasted.show("Profile Successfully Validated", {
              position: "top-right",
              duration: 5000,
              type: "success",
              closeOnSwipe: true
            });
            serverBus.$emit("users-fetch");
            this.loading = false;
            this.hideModal();
          })
          .catch(() => {
            this.loading = false;
            this.$toasted.show("Network error", {
              position: "top-right",
              duration: 5000,
              type: "default",
              closeOnSwipe: true
            });
          });
      }
    }
  }
};