<template>
  <div>
    <users-select @item-selected="fetchUser" />

    <reports v-if="$route.meta.isKYC" endpointType="brokers" />

    <b-card no-body class="col-12">
      <form
        @submit.prevent="
          fetchUser(null, searchColumnSelected, searchColumnValue)
        "
      >
        <b-row class="mt-4 d-flex align-items-end">
          <b-col md="3">
            <b-form-group horizontal label="Search By" class="mb-0">
              <b-form-select
                :options="searchColumnOptions"
                v-model="searchColumnSelected"
              />
            </b-form-group>
          </b-col>

          <b-col md="7">
            <b-form-group horizontal label="Value" class="mb-0">
              <b-input v-model="searchColumnValue" />
            </b-form-group>
          </b-col>

          <b-col md="2" sm="2">
            <button
              type="submit"
              class="btn btn-success btn-block"
              :disabled="!!searchColumnValue ? false : true"
            >
              Search
              <i class="fa fa-search"></i>
            </button>
          </b-col>
        </b-row>
      </form>

      <hr />

      <b-row class="d-flex align-items-center">
        <b-col md="6">
          <b-form-group horizontal label="Per page">
            <b-form-select
              :options="pageOptions"
              v-model="perPage"
              @change="(val) => setPerPage(val)"
            />
          </b-form-group>
        </b-col>

        <b-col md="4" sm="4">
          <b-form-group horizontal label="Sort">
            <b-input-group>
              <b-form-select v-model="sortBy" @change="() => listUsers()">
                <option :value="null">-- none --</option>
                <option value="rank_id">Rank</option>
                <option value="created_at">Created at</option>
                <option value="id">ID</option>
              </b-form-select>

              <b-form-select
                :disabled="!sortBy"
                v-model="sortDesc"
                @change="() => listUsers()"
                slot="append"
              >
                <option value="asc">Asc</option>
                <option value="desc">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="2" sm="2">
          <button
            @click="printTable(true, 'none')"
            class="btn btn-secondary btn-block mt-3"
          >
            Print
            <i class="fa fa-print"></i>
          </button>
        </b-col>
      </b-row>

      <b-table
        :hover="hover"
        :striped="striped"
        :bordered="bordered"
        :small="small"
        :fixed="fixed"
        responsive="sm"
        :items="items"
        :fields="fields"
        :current-page="currentPage"
        :per-page="perPage"
        :filter="filter"
        @filtered="onFiltered"
        :sort-by.sync="sortBy"
        :sort-direction="sortDirection"
        class="mt-3"
        :busy="loading"
      >
        <div slot="table-busy" class="text-center text-danger my-2">
          <strong>Loading...</strong>
        </div>

        <template v-slot:cell(username)="data">
          <span class="badge badge-primary">{{ data.item.username }}</span>
        </template>

        <template v-slot:cell(rank)="data">
          <span class="badge badge-warning">{{ data.item.rank }}</span>
        </template>

        <template v-slot:cell(total_directs)="data">
          <span class="font-weight-bold">{{ data.item.total_directs }}</span>
        </template>

        <template v-slot:cell(direct_valid_users)="data">
          <span class="font-weight-bold">{{
            data.item.direct_valid_users
          }}</span>
        </template>

        <template v-slot:cell(temp_user)="data">
          <span
            class="badge"
            :class="data.item.temp_user ? 'badge-danger' : 'badge-success'"
            >{{ data.item.temp_user ? "YES" : "NO" }}</span
          >
        </template>

        <template v-slot:cell(kyc_validated)="data">
          <span
            class="badge"
            :class="data.item.kyc_validated ? 'badge-success' : 'badge-danger'"
            >{{ data.item.kyc_validated ? "YES" : "NO" }}</span
          >
        </template>

        <template v-slot:cell(actions)="data">
          <template v-if="$route.meta.isKYC">
            <button
              class="btn btn-sm btn-info"
              @click="openKYCValidation(data.item)"
            >
              Review Documents
              <i class="fa fa-eye" />
            </button>
          </template>
          <template v-else>
            <router-link
              :to="'edit/' + data.item.uuid"
              class="btn btn-sm btn-dark"
            >
              Edit
              <i class="fa fa-pencil" />
            </router-link>

            <button
              @click="deleteUser(data.item)"
              class="btn btn-sm btn-danger ml-1"
            >
              Delete
              <i class="fa fa-times" />
            </button>
          </template>
        </template>
      </b-table>

      <nav>
        <paginate
          :force-page="pageNo"
          :page-count="pageCount"
          :page-range="3"
          :click-handler="nextPageCall"
          :prev-text="'Prev'"
          :next-text="'Next'"
          :container-class="'pagination b-pagination pagination-md'"
          :prev-class="'page-item'"
          :prev-link-class="'page-link'"
          :next-class="'page-item'"
          :next-link-class="'page-link'"
          :page-class="'page-item'"
          :page-link-class="'page-link'"
        ></paginate>
      </nav>
    </b-card>
    <kyc-validation />
  </div>
</template>

<script>
import NavTop from "./NavTop/";
import { NavCommon } from "@/mixins/";
import { serverBus } from "@/main";
import UsersSelect from "@/components/UsersSelect";
import KYCValidation from "./KYCValidation/";
import Reports from "@/components/QuickReports";

import _ from "lodash";
import Paginate from "vuejs-paginate";

export default {
  name: "users-list",
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: true,
    },
    striped: {
      type: Boolean,
      default: true,
    },
    bordered: {
      type: Boolean,
      default: true,
    },
    small: {
      type: Boolean,
      default: true,
    },
    fixed: {
      type: Boolean,
      default: false,
    },
  },
  mixins: [NavCommon],
  components: {
    "top-nav": NavTop,
    paginate: Paginate,
    UsersSelect,
    Reports,
    "kyc-validation": KYCValidation,
  },
  data() {
    return {
      items: [],
      fields: [
        { key: "id", sortable: true, label: "ID" },
        { key: "username", sortable: false, label: "Username" },
        { key: "email", sortable: false, label: "Email" },
        { key: "rank", sortable: false, label: "Rank" },
        {
          key: "direct_valid_users",
          sortable: true,
          label: "Direct Valid Lines",
          class: "text-center",
        },
        {
          key: "total_directs",
          sortable: true,
          label: "Direct Lines",
          class: "text-center",
        },
        {
          key: "temp_user",
          label: "Temp User",
          class: "text-center",
        },
        {
          key: "kyc_validated",
          label: "KYC Validated",
          class: "text-center",
        },
        {
          key: "created_at",
          sortable: true,
          label: "Created At",
          class: "text-right",
        },
        {
          key: "actions",
          label: "Actions",
          class: "text-right",
        },
      ],
      pageNo: 1,
      currentPage: 1,
      perPage: 30,
      totalRows: 0,
      pageCount: 1,
      loaders: {},
      show: true,
      loading: false,
      filter: null,
      sortBy: null,
      sortDesc: "desc",
      sortDirection: "asc",
      username: "",
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 100, 250, 500, 1000],
      searchColumnOptions: [
        "email",
        "username",
        "name",
        "last_name",
        "country",
        "city",
        "address",
      ],
      searchColumnSelected: "email",
      searchColumnValue: "",
    };
  },
  mounted() {
    this.listUsers();
    window.onafterprint = () => {
      this.printTable(false, "");
    };
  },
  created() {
    this.listUsers();
    serverBus.$on("users-fetch", () => {
      this.listUsers();
    });
  },
  watch: {
    $route(to, from) {
      if (to.name !== from.name) this.listUsers();
    },
  },
  methods: {
    setPerPage(val) {
      this.listUsers();
    },
    setOrder(val) {
      this.listUsers();
    },
    nextPageCall(pageNum) {
      this.listUsers(pageNum);
    },
    printTable(print, myClass) {
      var cols = document.getElementsByClassName("print__action");
      Array.prototype.forEach.call(cols, function(el) {
        el.style.display = myClass;
      });

      if (print) window.print();
    },
    openKYCValidation(item) {
      serverBus.$emit("kyc-modal-validation", item);
    },
    openDetailModal(item) {
      serverBus.$emit("detailUserModal", item);
    },
    deleteUser(item) {
      if (this.validateDelete()) {
        this.$http({
          url: "/api/v1/admin/users/" + item.uuid,
          data: this.form,
          method: "DELETE",
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken,
          },
        })
          .then((response) => {
            if (response.status == 200) {
              this.$toasted.show("User successfully destroyed", {
                position: "top-right",
                duration: 5000,
                type: "danger",
                closeOnSwipe: true,
              });
              this.listUsers();
            }
          })
          .catch((error) => {
            error.response.data.map((m) => {
              this.$toasted.show(m, {
                position: "top-right",
                duration: 5000,
                type: "error",
                closeOnSwipe: true,
              });
            });
          });
      }
    },
    fetchUser(username, column = null, pattern = null) {
      this.loading = true;
      let url = `1?username=${username}`;
      if (column && pattern) {
        url = `search?column=${column}&pattern=${pattern}`;
      }
      this.$http(`/api/v1/admin/users/${url}`, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken,
        },
      })
        .then((response) => {
          this.items = response.data;
          this.totalItems = response.data.length;
          this.perPage = response.data.length;
          this.pageCount = 1;
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },
    listUsers(pageNo = 1) {
      this.loading = true;
      this.pageNo = pageNo;
      let orderBy = this.sortBy ? "&order_by=" + this.sortBy : "";
      let sortDesc = this.sortDesc ? "&sort_by=" + this.sortDesc : "";
      let kycParams = this.$route.meta.isKYC
        ? "&kyc_verification=true&kyc_validated"
        : "";

      this.$http(
        "/api/v1/admin/users?per_page=" +
          this.perPage +
          "&page=" +
          pageNo +
          orderBy +
          sortDesc +
          kycParams,
        {
          headers: {
            "content-type": "application/json",
            Authorization: this.$store.getters.authToken,
          },
        }
      )
        .then((response) => {
          this.items = response.data;
          this.totalItems = parseInt(response.headers.total);
          this.perPage = parseInt(response.headers["per-page"]);
          this.pageCount = Math.ceil(
            parseFloat(
              response.headers["per-page-total"] /
                parseInt(response.headers["per-page"])
            )
          );
          this.loading = false;
        })
        .catch((error) => {
          error.response.data.map((m) => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true,
            });
            this.loading = false;
          });
        });
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
  },
};
</script>
